* {
  box-sizing: border-box;
}

html {
  font-family: 'Comfortaa', cursive;
}

body {
  margin: 0;
  background-image: url(/expenses-app-v1/static/media/circle-scatter-haikei.f4521cf4.svg);
  background-color: #1E2528;
}

.app-h2 {
  text-align: center;
  color: #fefefe;
  font-size: 38px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.app-span {
  color: #0EF6BE;
  display: block;
  margin: 6px 3px 0;
}


.expenses-filter {
  color: white;
  padding: 0;
}
  
.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  text-align: center;
}
  
.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0;
  margin-right: 25px;
}
  
.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  height: 42px;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  background: transparent;
  background-image: url("data:image/svg+xml;utf8,<svg fill='turquoise' height='32' viewBox='0 0 24 24' width='35' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-position-y: 5px;
  border: 1px solid #0EF6BE;
  border-radius: none;
  padding: 1rem;
  width: 150px;
  color: #0EF6BE;
}

option {
  color: #1E2528;
}
.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #f7f7f7;
    background-color: #1E2528;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
}
  
.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}
  
.expense-date__year {
  font-size: 0.75rem;
}
  
.expense-date__day {
  font-size: 26px;
  font-weight: bold;
  color: #0EF6BE;
  margin: 4px 0;
}
.card {
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 40px 0;
  background-color: #3A504B95;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2 {
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #1E2528;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 6px;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
    color: #0EF6BE;
  }
}
.expenses-list {
    list-style: none;
    padding: 0;
}
  
.expenses-list__fallback {
    color: white;
    text-align: center;
}
.chart {
    padding: 1rem;
    border-radius: 6px;
    background-color: #04624C30;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
}
.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20px;
}
  
.chart-bar__inner {
    height: 100%;
    width: 100%;
    min-width: 18px;
    border-radius: 6px;
    background-color: #1E252898;
    border: 1px solid #0EF6BE80;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
  
.chart-bar__fill {
    background-color: #0EF6BE;
    width: 100%;
    transition: all 0.3s ease-out;
}
  
.chart-bar__label {
    font-weight: bold;
    font-size: 10px;
    text-align: center;
    color: #FFF;
    text-transform: lowercase;
    display: block;
    margin-top: 10px;
}
.expenses {
    padding: 1rem;
    background-color: transparent;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    box-shadow: 0 10px 50px #0EF6BE20;
}

.no-expenses-found {
    color: red;
}
.new-expense {
  background-color: #1E2528;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  text-align: center;
  box-shadow: 0 20px 50px #1E2528;
  border: 1px solid #0EF6BE80;
}
  
.new-expense button {
  font-size: 16px;
  font-weight: bold;
  color: #FFFFFF;
  background-color: #1E252898;
  border: 2px solid #0EF6BE;
  padding: 18px;
  border-radius: 12px;
  cursor: pointer;
}

.new-expense button[type="button"] {
  margin-right: 20px;
}
  
.new-expense button:hover,
.new-expense button:active {
  background-color: #0EF6BE99;
  border-color: #0EF6BE;
  color: #000000;
}
  
.new-expense__control label {
  color: white;
}
.new-expense__controls {
  display: flex;
  flex-direction: column;
  text-align: left;
}
  
.new-expense__control label {
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 0.5rem;
  display: block;
}
  
.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  height: 42px;
  width: 100%;
}
  
.new-expense__actions {
  text-align: right;
  margin: 30px auto;
  width: 60%;
}

.new-expense__control {
  width: 60%;
  margin: 10px auto;
}
